
// PLAY BTN --------------------------------------------//

.dhr-playbtn {
	display: inline-block;
	position: relative; 
	width: 10em; height: 10em;
	border-radius: 50%;
	overflow:hidden;
	transition: transform .2s ease;

	svg {
		position: absolute;
		top:0; left:0; width:100%; height:100%;

		circle {
			fill: none;
			stroke-width: 10;
		}

		&.dhr-playbtn--circ-back {
			circle {
				stroke: rgba($white,0.5);
			}
		}
		&.dhr-playbtn--circ-front {
			transform: rotate(-90deg);
			transition: transform 1s $ease-in-out-quart;
			circle {
				stroke: $primary;
				stroke-dasharray: 580;
				stroke-dashoffset: 580;
				opacity: 0;
				transition: stroke-dashoffset 0.1s $ease-in-out-quart 0.4s, opacity 0.35s linear;
			}
		}
	}
	

	&--icon {
		display:block; position:absolute;
		text-align: center;
		line-height: 0;
		top:50%; left:0; width:100%;
		font-size: 1.65em;
		transform: translateY(-50%) scale(1);
		i {
			display:block;
			transition: all 1s ease;
		}
	}
	

	&:hover {
		svg.dhr-playbtn--circ-front {
			transform: rotate(-90deg);

			circle {
				transition: stroke-dashoffset 0.8s $ease-in-out-quart, opacity 0.5s linear;
			}
		}
		
		svg.dhr-playbtn--circ-front circle {
			opacity: 1;
			stroke-dashoffset: 0;
		}

		.dhr-playbtn--icon {
			i { 
				color: $white; 
				transition-delay: 0.5s;
			}
		}
	}
	

	&:active {
		transform: scale(0.95);
	}
}


